.projects__infoAndLink {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  justify-content: center;
  align-content: center;
  transform: translate(-50%, -50%);
  display: none;
}
.projects__infoAndLink .btn{
  width: 60%;
  justify-self: center;
  margin-top: 1rem;
  font-size: 90%;
  padding: 0.5rem 0;
}
.projects__container{
    justify-self: center;
    grid-column: 1/-1;
    margin-bottom: 2rem;
    position: relative;
    transition: all 1s;
}
  .projects__container img{
    width: 100%;
  }

  .projects__infoAndLink h1{
    font-size: 100%;
  }

  /*pc*/
  @media (min-width:1025px){


  #project_1, #project_3, #project_5, #project_7, #project_9, #project_11, #project_13, #project_15, #project_17, #project_19{
    grid-column: 1/6;
    width: 100%;
    height: 90%;
    margin-bottom: 2rem;
}
#project_2, #project_4, #project_6, #project_8, #project_10, #project_12, #project_14, #project_16, #project_18, #project_20{
    grid-column: 7/12;
    width: 100%;
    height: 90%;
    margin-bottom: 2rem;
}
.projects__container img{
  width: 100%;
}
}
