.services__container{
    grid-column: 1/-1;
    justify-self: center;
    width: 100%;
    margin-bottom: 2rem;
}
.services__title{
    font-size: 1.5rem;
}
.services__logo{
    font-size: 4rem;
}

/*tab*/

@media (min-width:701px) and (max-width:1024px){
    #services .row{
        grid-template-columns: repeat(8,1fr);
    }
    #service_1, #service_3, #service_5, #service_7, #service_9, #service_11, #service_13, #service_15, #service_17, #service_19{
        grid-column: 1/5;
        width: 90%;
        height: 90%;
        margin-bottom: 2rem;
    }
    #service_2, #service_4, #service_6, #service_8, #service_10, #service_12, #service_14, #service_16, #service_18, #service_20{
        grid-column: 5/9;
        width: 90%;
        height: 90%;
        margin-bottom: 2rem;
    }
}

/*pc*/

@media (min-width:1025px){
    #services .row{
        grid-template-columns: repeat(12,1fr);
    }
    #service_1, #service_4, #service_7, #service_10, #service_13, #service_16, #service_19, #service_21, #service_24{
        grid-column: 1/5;
        width: 90%;
        height: 90%;
        margin-bottom: 2rem;
    }
    #service_2, #service_5, #service_8, #service_11, #service_14, #service_17, #service_20, #service_23, #service_26, #service_29{
        grid-column: 5/9;
        width: 90%;
        height: 90%;
        margin-bottom: 2rem;
    }
    #service_3, #service_6, #service_9, #service_12, #service_15, #service_18, #service_21, #service_24, #service_27, #service_30{
      grid-column: 9/13;
      width: 90%;
      height: 90%;
      margin-bottom: 2rem;
  }

  #project_1, #project_3, #project_5, #project_7, #project_9, #project_11, #project_13, #project_15, #project_17, #project_19{
    grid-column: 1/6;
    width: 90%;
    height: 90%;
    margin-bottom: 2rem;
}
#project_2, #project_4, #project_6, #project_8, #project_10, #project_12, #project_14, #project_16, #project_18, #project_20{
    grid-column: 6/12;
    width: 90%;
    height: 90%;
    margin-bottom: 2rem;
}
}