.team__container{
    justify-self: center;
    grid-column: 1/-1;
    width: 200px;
        height: 200px;
    margin-bottom: 2rem;
    position: relative;
  }

.team_info{
  position: absolute;
  background-color: rgba(233, 32, 79, 0.8);
  width: 100%;
  height: 100%;
  border-radius: 50%;
display: none;
align-content: center;
justify-content: center;
}
.team_job{
margin: 1rem 0;
font-size: 55%;
}
.team_info h3{
  font-size: 110%;
}
.team_info ul{
  display: flex;
justify-self: center;
}
.team_info ul li{
  padding: 0 0.5rem;
  font-size: 100%;
}

  @media (min-width:701px) and (max-width:1024px){
    #team .row{
      grid-template-columns: repeat(9,1fr);
    }
    #team_1, #team_4, #team_7, #team_10, #team_13, #team_16, #team_19, #team_21, #team_24{
        grid-column: 4/7;
        width: 200px;
        height: 200px;
        margin-bottom: 2rem;
    }
    #team_2, #team_5, #team_8, #team_11, #team_14, #team_17, #team_20, #team_23, #team_26, #team_29{
        grid-column: 1/4;
        width: 200px;
        height: 200px;
        margin-bottom: 2rem;
    }
    #team_3, #team_6, #team_9, #team_12, #team_15, #team_18, #team_21, #team_24, #team_27, #team_30{
      grid-column: 7/10;
      width: 200px;
      height: 200px;
      margin-bottom: 2rem;
  }
}

@media (min-width:1025px){
  .team__container{
    width: 270px;
    height: 270px;
  }
}
